import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import png from "../asserts/chatGpt.jpg";
import { ElMessage } from "element-plus";
export default {
  setup() {
    const router = useRouter();
    const path = window.location.pathname; // 假设是 "/api/getData"
    const parts = path.split("/"); // 分割路径，得到 ["", "api", "getData"]
    let prefix = ""; // 默认值为 ""

    if (parts.length >= 3) {
      prefix = "/" + parts[1]; // 如果parts长度大于或等于3，获取第一个部分
    }

    const username = ref("");
    const password = ref("");
    const checked = ref("");
    const image = png;
    const page = ref(true);
    onMounted(async () => {
      const savedUsername = localStorage.getItem("savedUsername");
      const savedPassword = localStorage.getItem("savedPassword");
      const savedRemember = localStorage.getItem("savedRemember");
      if (savedRemember === "true") {
        username.value = savedUsername || "";
        password.value = savedPassword || "";
        checked.value = true;
      }
      if (window.innerWidth > 767) {
        page.value = false;
      }
      fetchLoginToken();
    });

    // 自定义校验函数，直接返回错误提示
    const customValidator = value => {
      // 不允许有空格和回车符
      const regex = /^(?!.*[\s\r\n]).*$/;
      if (regex.test(value)) {
        return true;
      } else {
        return "此项不允许包含空格和回车符";
      }
    };

    /**
     * 用jwt令牌验证身份
     * 未通过者返回到/login
     */
    const token = localStorage.getItem("jwtToken"); // 从localStorage获取JWT令牌
    const headers = {
      Authorization: `Bearer ${token}`
    };
    /**
     * 用jwt令牌验证身份函数
     */
    const fetchLoginToken = async () => {
      axios.post(`${prefix}/api/loginToken?token=` + token).then(response => {
        if (response.data.code == 1) {
          if (window.innerWidth <= 1000) {
            router.replace(`/iphone`);
          } else {
            router.replace(`/home`);
          }
          return;
        } else {
          ElMessage.error("令牌失效，请重新登录！");
        }
      }).catch(error => {
        ElMessage.error("令牌失效，请重新登录！");
      });
    };
    const submit = () => {
      // 处理登录逻辑
      if (checked.value) {
        // 如果记住密码被选中，将用户名和密码保存到本地存储
        localStorage.setItem("savedUsername", username.value);
        localStorage.setItem("savedPassword", password.value);
        localStorage.setItem("savedRemember", "true");
      } else {
        // 如果不记住密码，清除本地存储中的信息
        localStorage.removeItem("savedUsername");
        localStorage.removeItem("savedPassword");
        localStorage.removeItem("savedRemember");
      }
      let setting = {
        loginUsername: username.value,
        loginPassword: password.value
      };
      fetch(`${prefix}/api/login`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${setting}`
        },
        body: JSON.stringify(setting)
      }).then(response => response.json()).then(data => {
        if (data.code === 1) {
          const token = data.data;
          localStorage.setItem("jwtToken", token);
          ElMessage.success("登录成功！");
          setTimeout(() => {
            if (window.innerWidth <= 1000) {
              router.replace(`/iphone`);
            } else {
              router.replace(`/home`);
            }
          }, 1000);
        } else {
          ElMessage.error(data.msg);
        }
      }).catch(error => {
        console.error("登录时出现错误:", error);
        ElMessage.error("账号或密码错误！");
      });
    };
    return {
      username,
      password,
      image,
      checked,
      submit,
      page
    };
  }
};